import { createSlice } from "@reduxjs/toolkit";
import LocalStorageHelper from "../utils/localStorageHelper";

export const userKEY = "currentUser";
export const { actions: CurrentUserActions, reducer: CurrentUserReducer } = createSlice({
  name: "user",
  initialState: {
    user: LocalStorageHelper.get(userKEY),
  },
  reducers: {
    setUserState: (state, { payload }) => {
      payload
        ? LocalStorageHelper.store(userKEY, payload)
        : LocalStorageHelper.delete(userKEY);
      state.user = payload;
    },
  },
});
