import { Box, Col, Content, Infobox2, Row } from "adminlte-2-react";
import React, { useEffect, useState } from "react";
import CommonService from "src/services/CommonService";


import {
  REGISTRATION_LIST_PAGE,
  USER_LIST_PAGE,
  NMS_LIST_PAGE,
  DEVICE_MAMAGEMENT_ROUTE,
  SUPER_USER_LIST_PAGE,
  ONLINE_USERS_ROUTE} from "src/constants/global";

const DashboardView = ({
  title = "System Overview",
  description,
  ...rest
}) => {

  const NAVIGATION_CARDS = {
    totalUsers: { text: "Total Users", icon: "fa-users", to: USER_LIST_PAGE, color: 'orange' },
    totalOnlineUsers: { text: "Total Online Users", icon: "fa-users", to: ONLINE_USERS_ROUTE, color: 'teal' },
    totalSuperUsers: { text: "Total Super Users", icon: "fa-users", to: SUPER_USER_LIST_PAGE, color: 'purple' },
    totalNmsDevices: { text: "Total Nms Monitored Devices", icon: "fa-laptop", to: NMS_LIST_PAGE, color: 'purple' },
    totalDevices: { text: "Total devices in directory", icon: "fa-laptop", to: DEVICE_MAMAGEMENT_ROUTE, color: 'orange' },
    totalPendingRequests: { text: "Pending registration requests", icon: "fa-tasks", to: REGISTRATION_LIST_PAGE, color: 'teal' }
  }

  const [summary, setSummary] = useState({
    totalUsers: 0,
    totalOnlineUsers: 0,
    totalSuperUsers: 0,
    totalNmsDevices: 0,
    totalDevices: 0,
    totalPendingRequests: 0
  })

  useEffect(() => {
    (async () => {
      const data = await CommonService.getDashboardSummary()
      data?.data && setSummary(data.data)
    })()
  }, [])

  return (

    <Content title={[title]} subTitle={description} browserTitle={title}>
        <Row>
          {Object.keys(NAVIGATION_CARDS).map((key) => {
            const card = NAVIGATION_CARDS[key]
            return (
              <Col key={key} md={4}>
                <Box color={card.color} type="success" icon={card.icon}>
                  <Infobox2 title={summary[key] || 0} {...card} />
                </Box>
              </Col>
            );
          })}
        </Row>
    </Content>
  );
};


export default DashboardView