import httpClient from "../utils/httpClient";

const loginUser = async (username, password) =>
  await httpClient.post("/auth/login", {
    username,
    password
  });

const getCurrentUser = async () => await httpClient.get("/api/v1/me");

const logOutUser = async () => await httpClient.get("/auth/logout");

const getAllUsers = async (params = {}) =>
  await httpClient.get("/api/v1/admin/users", { params });

const rejectUser = async (id) => await httpClient.delete(`/api/v1/admin/users/${id}`);
const approveUser = async (id) => await httpClient.post(`/api/v1/admin/users/${id}`);

const blockUser = async (id) => await httpClient.patch(`/api/v1/admin/users/${id}/block`)
const unblockUser = async (id) => await httpClient.patch(`/api/v1/admin/users/${id}/unblock`)

const UserService = {
  loginUser,
  getCurrentUser,
  logOutUser,
  getAllUsers,
  approveUser,
  rejectUser,
  blockUser,
  unblockUser
};

export default UserService;
