import { Callout } from "adminlte-2-react";
import React from "react";
import { connect } from "react-redux";
import { dismissGlobalError } from "src/utils/storeHelper";
import Dialog from "./Dialog";

const GlobalToast = ({
    globalMessage = {}
}) => {
    if (!globalMessage || !globalMessage.message) {
        return <React.Fragment />
    }
    return (
        <Dialog
            canDismiss={true}
            title={''}
            size='xl'
            show={globalMessage.message}
            zIndex={9999}
            onDismiss={() => {
                dismissGlobalError();
            }}>
            <Callout className="rounded-0" title={globalMessage.title}
                type={globalMessage.type}>{globalMessage.message}</Callout>
        </Dialog>
    )

}

const mapStateToProps = (state) => {
    return {
        globalMessage: state.globalState?.globalMessage || {}
    };
};


export default connect(mapStateToProps, null)(GlobalToast);
