const { default: httpClient } = require("src/utils/httpClient");

const getLocationList = async () => {
    const data = await httpClient.get("/api/v1/locations");
    const locations = data?.success ? data.data : []
    return locations
}

const getBranchesList = async () => {
    const data = await httpClient.get("/api/v1/branches");
    return data?.success ? data.data : []
}


const getRanksList = async () => {
    const data = await httpClient.get("/api/v1/ranks");
    return data?.success ? data.data : []
}


const createDatabaseBackup = async () => await httpClient.post(`/api/v1/admin/db-backups`)
const getDashboardSummary = async () => await httpClient.get(`/api/v1/admin/dashboard`)


const CommonServerice = {
    getLocationList,
    getBranchesList,
    getRanksList,
    createDatabaseBackup,
    getDashboardSummary

}
export default CommonServerice
