import { Col, Row, Button, ButtonGroup } from "adminlte-2-react";
import * as yup from "yup";
import React, { useCallback, useEffect, useMemo, useState } from "react";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import BootstrapTable from "react-bootstrap-table-next";
import filterFactory from 'react-bootstrap-table2-filter';
import paginationFactory from 'react-bootstrap-table2-paginator';
import overlayFactory from 'react-bootstrap-table2-overlay';
import { parseQueryParams, pushQueryParams } from "src/utils/routerHelper";
import LoadingOverlay from "react-loading-overlay"
import Dialog from "./Dialog";
import EntityForm from "./EntityForm";
import { showGlobalModalNotification } from "src/utils/storeHelper";
import httpClient from "src/utils/httpClient";
LoadingOverlay.propTypes = undefined

const MODE_EDIT = 'edit';
const MODE_ADD = 'add';

const DataGridComponent = ({
    modalSize = 'xl',
    keyField = "id",
    entitySingle = 'record',
    entityPlural = 'records',
    columns = [],
    hideIdOnEdit = false,
    data = {
        results: [],
        currentPage: 1,
        totalResults: 0,
        limit: 100,
        totalPages: 1,
    },
    remote = {
        sort: true,
        filter: true,
        pagination: true,
    },
    defaultSorted = [
        { dataField: 'id', order: 'asc' }
    ],
    showTotal = true,
    beforeSave = (values) => { return values },
    onTableChange = async (params) => { },
    onDataUpdated = async (tableData) => { },
    onDelete = (id) => { },
    canAdd = true,
    canDelete = true,
    canEdit = true,
    rowActionButtons = [],
    globalActionButtons = [],
    formOptions = {
        fields: [],
        validationSchema: null,
        onSuccess: async (data) => { },
    },
    resource = null,
    onFormShow = (row, mode) => {

    },
    actionColumnWidth = 200,
    ...rest
}) => {
    const [formErrors, setFormErrors] = useState({})
    const [record, setCurrentRecord] = useState(null)
    const [showFormDialog, setShowFormDialog] = useState(false)
    const [selectedIds, setSelectedIds] = useState([])
    const [loading, setLoading] = useState(false)
    const [records, setRecords] = useState(data)
    const fetchTableData = useCallback(async () => {
        setFormErrors({})
        if (resource) {
            const params = parseQueryParams(rest.history)
            const result = await httpClient.get(`${resource}`, { params })
            if (result?.success) {
                setRecords({
                    results: result.data?.results || [],
                    currentPage: result.data?.currentPage || 1,
                    totalResults: result.data.totalResults || 0,
                    limit: result.data?.limit || 100,
                    totalPages: result.data?.totalPages || 1
                })
            } else {
                setRecords({ ...data, results: [], totalResults: 0, limit: 100 })
            }
        }
    }, [resource, data, rest.history])

    const columnsWithActions = useMemo(() => {
        const clms = columns.filter(clm => clm.dataField !== '_actions')
        if (!(canEdit || canDelete || rowActionButtons.length > 0)) {
            return clms;
        }
        clms.push({
            dataField: '_actions',
            isDummyField: true,
            style: {
                width: actionColumnWidth || 200,
            },
            text: 'Actions',
            formatter: (cellContent, row) => {
                return (
                    <ButtonGroup>
                        {rowActionButtons.map(actionButton => {
                            actionButton.canShow = actionButton.canShow ? actionButton.canShow : () => true
                            if (actionButton.canShow && actionButton.canShow(row)) {
                                return <Button key={`action-${row.id}-${actionButton.text}`} className="mr-2"
                                    type={actionButton?.type || 'primary'}
                                    icon={actionButton?.icon || 'gear'}
                                    text={actionButton.text}
                                    onClick={async () => {
                                        await actionButton.onClick(row.id, row)
                                        fetchTableData()
                                    }}
                                />
                            }

                        })}
                        {canEdit && <Button className="mr-2" type="primary" text="Edit" icon="fa-edit" onClick={() => {
                            pushQueryParams(rest.history, { id: row.id, mode: MODE_EDIT }, true)
                            setCurrentRecord(row);
                            setShowFormDialog(true)
                            setFormErrors({})
                        }} />}
                        {canDelete && <Button className="mr-2" type="danger" onClick={async () => {
                            if (resource) {
                                const data = await httpClient.delete(`${resource}/${row.id}`)
                                if (data?.success) {
                                    showGlobalModalNotification({
                                        success: true,
                                        message: `${entitySingle} successfully deleted.`
                                    })
                                } else {
                                    setFormErrors(data?.errors ?? {})
                                }
                            } else {
                                await onDelete(row.id);
                            }
                            fetchTableData()
                        }} text="Delete" icon="fa-trash" />
                        }

                    </ButtonGroup>
                )
            },
        })
        return clms
    }, [columns, canDelete, canAdd, onDelete, rowActionButtons, rest.history])

    const validationSchemaDynamic = useMemo(() => {
        return formOptions.validationSchema || yup.object().shape({})
    }, [formOptions.validationSchema]);

    const fieldsDyanmic = useMemo(() => {
        return formOptions.fields;
    }, [formOptions.fields]);

    const initialValues = useMemo(() => {
        const values = {}
        fieldsDyanmic.forEach(field => {
            values[field.recordKey || field.name] = record ? (record[field.recordKey] || record[field.name] || field.default) || '' : (field.default || '')
        })
        values['id'] = record ? record.id : null
        return values;
    }, [record, fieldsDyanmic]);

    const triggerSetCurrentRecord = () => {
        const params = parseQueryParams(rest.history)
        const mode = params['mode'] || ''
        const id = params['id'] || null
        const isAddEdit = mode === MODE_ADD || mode === MODE_EDIT
        const currentRecord = (records.results || []).find(r => `${r.id}` === id)
        setCurrentRecord(currentRecord)
        setShowFormDialog(isAddEdit)
    }

    useEffect(() => {
        fetchTableData();
        triggerSetCurrentRecord();
        const unlisten = rest.history.listen((...args) => {
            if (args[1] === 'POP') {
                fetchTableData()
            }
        })
        return unlisten
    }, [])

    useEffect(() => {
        triggerSetCurrentRecord();
        onDataUpdated(records);
    }, [showFormDialog, records])

    useEffect(() => {
        onFormShow(record, parseQueryParams(rest.history, 'mode'))
    }, [record, rest.history])

    return (
        <React.Fragment>
            {(canAdd || canEdit) && <Dialog size={modalSize} show={showFormDialog}
                showHeader={true}
                title={`${record ? `Editing ${entitySingle} ${!hideIdOnEdit ? `#${record.id}` : ''}` : `Adding ${entitySingle}`}`}
                onDismiss={() => {
                    setShowFormDialog(false)
                    pushQueryParams(rest.history, { mode: null, id: null }, true, true)
                }}>
                <EntityForm
                    entityPlural={entityPlural}
                    entitySingle={entitySingle}
                    formErrors={formErrors}
                    fields={fieldsDyanmic}
                    initialValues={initialValues}
                    editMode={record ? true : false}
                    onSubmit={async (values, { resetForm }) => {
                        window.scrollTo(0, 0);
                        values = beforeSave(values)
                        let data
                        if (resource) {
                            data = values?.id ? await httpClient.put(`${resource}/${values.id}`, values) : await httpClient.post(resource, values)
                        } else {
                            data = await formOptions.onSuccess(values)
                        }

                        if (data?.success) {
                            resetForm();
                            setCurrentRecord(null);
                            setShowFormDialog(false);
                            pushQueryParams(rest.history, { mode: null, id: null }, true, true)
                            onTableChange(parseQueryParams(rest.history))
                            fetchTableData();
                            showGlobalModalNotification({
                                success: true,
                                message: `${entitySingle} successfully ${values?.id ? 'updated' : 'created'}`
                            })
                        } else {
                            setFormErrors(data?.errors ?? {})
                        }

                    }}
                />
            </Dialog>
            }
            <ToolkitProvider
                keyField={keyField}
                data={records.results || []}
                columns={columnsWithActions}
            >
                {
                    props => (
                        <div>
                            <Row className="text-right">
                                <Col md={12}>
                                    <Button onClick={async () => {
                                        setLoading(true)
                                        fetchTableData()
                                        await onTableChange(parseQueryParams(rest.history))
                                        setLoading(false)
                                    }} className="mr-2" type="primary" disabled={loading} text={loading ? "Loading..." : "Refresh"} icon="fa-redo" />
                                    {canAdd &&
                                        <Button className="mr-2" onClick={() => {
                                            pushQueryParams(rest.history, { mode: MODE_ADD, id: undefined }, true)
                                            setShowFormDialog(true)
                                            setCurrentRecord(null);
                                        }} type="success" text={`Add ${entitySingle}`} icon="fa-plus-circle" />
                                    }
                                    {globalActionButtons.map(actionButton => {
                                        return <Button className="mr-2"
                                            type={actionButton.type}
                                            icon={actionButton.icon}
                                            text={actionButton.text}
                                            onClick={async () => {
                                                await actionButton.onClick(selectedIds)
                                                fetchTableData();
                                            }}
                                        />
                                    })}
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <hr />
                                </Col>
                            </Row>
                            <BootstrapTable
                                {...props.baseProps}
                                remote={remote}
                                defaultSorted={defaultSorted}
                                
                                pagination={records.totalPages > 1 ? paginationFactory({
                                    page: records.currentPage || 1,
                                    sizePerPage: records.limit || 100,
                                    totalSize: records.totalResults || records.results.length || 0,
                                    sizePerPageList: [100, 200, 500],
                                    paginationSize: records.totalResults || records.results.length || 0,
                                    showTotal,
                                }): null}
                                loading={loading}
                                filter={filterFactory()}
                                onTableChange={async (type, _data) => {
                                    const params = {
                                        sortOrder: _data['sortOrder'],
                                        sortBy: _data['sortField'],
                                        page: _data['page'],
                                        limit: _data['sizePerPage'],
                                    }
                                    pushQueryParams(rest.history, params)
                                    fetchTableData();
                                    setLoading(true)
                                    await onTableChange(params)
                                    setLoading(false)
                                }}
                                striped={true}
                                selectRow={(globalActionButtons.length || canEdit || canDelete) ? {
                                    mode: 'checkbox',
                                    bgColor: '#eee',
                                    selectColumnStyle: () => {
                                        return {
                                            width: 20
                                        }
                                    },
                                    onSelectAll: (isSelected) => {
                                        const selections = []
                                        if (isSelected) {
                                            (data.results || []).forEach(r => {
                                                selections.push(selections)
                                            })
                                        }
                                        setSelectedIds(selections)
                                    },
                                    onSelect: (row, isSelected) => {
                                        const selections = selectedIds.filter(id => row.id !== id)
                                        if (isSelected) {
                                            selections.push(row.id)
                                        }
                                        setSelectedIds(selections)
                                    }
                                } : {
                                    mode:'ROW_SELECT_DISABLED',
                                    hideSelectColumn: true
                                }}
                                overlay={overlayFactory({
                                    spinner: true,
                                    styles: {
                                        overlay: (base) => ({ ...base, background: 'rgba(0, 0, 0, 0.5)', position: 'absolute' })
                                    }
                                })}
                                noDataIndication={() => <h5 className="text-center">Sorry no {entityPlural} currently availble</h5>} />
                        </div>
                    )
                }
            </ToolkitProvider>
        </React.Fragment>

    )
}

export default DataGridComponent;