import axios from "axios";
import store from "src/store/store";
import { CurrentUserActions } from "src/slices/currentUserSlice";
import LocalStorageHelper from "./localStorageHelper";
import { showGlobalModalNotification } from "./storeHelper";

const baseUrl = `/`;
const httpClient = axios.create({
  baseURL: baseUrl,
  headers: {
    "Content-type": "application/json",
  },
});


httpClient.interceptors.request.use((config) => {
  // Do something before request is sent
  const user = LocalStorageHelper.get('currentUser');
  config.headers['Content-type'] = `application/json`
  config.headers['Authorization'] = `Bearer ${user?.accessToken}`
  return config;
});

httpClient.interceptors.response.use(
  (response) => {
    return { data: response.data, success: true };
  },
  (error) => {
    const errorResponse = error?.response;
    switch (errorResponse?.status) {
      case 422:
        const errors = errorResponse?.data ?? {}
        const globalError = errors['global'] || null
        if (globalError){
          showGlobalModalNotification({
            message: globalError,
            success: false,
            hideTs: 5000,
            title: "Error!!"
          })
        }
        return Promise.resolve({
          success: false,
          errors: errors,
          code: 422,
        });
      case 401:
        const redirectURL = errorResponse.request.responseURL
        store.dispatch(CurrentUserActions.setUserState(null));
        window.location = `/login?redirect=${encodeURIComponent(redirectURL)}`
        return Promise.resolve({
          success: false,
          code: 401,
        });
      case 404:
        showGlobalModalNotification({
          message: 'Page/Resource you are trying to access, do not exist.',
          success: false,
          hideTs: 5000,
          title: "Error!!"
        })
        return Promise.resolve({
          success: false,
          code: 404,
        });
      default:
        showGlobalModalNotification({
          message: "Something went wrong.",
          success: false,
          hideTs: 5000,
          title: "Error!!"
        })

    }
    return Promise.reject(error);
  }
);

export default httpClient;
