import { createSlice } from "@reduxjs/toolkit";

export const { reducer: RegistrationReducer, actions: RegistrationActions } = createSlice(
  {
    name: "registrationSlice",
    initialState: {
      listData: {
        results: [],
        currentPage: 1,
        totalResults: 0,
        totalPages: 1,
      },
    },
    reducers: {
      setRegistrations: (state, { payload }) => {
        state.listData = { ...payload }
      },
    },
  }
);
