import { Button } from "adminlte-2-react";
import React from "react";

const Dialog = ({
  children = "",
  title = "Dialog",
  exists = true,
  show = true,
  showHeader = true,
  acceptText = null,
  cancelText = null,
  canDismiss = true,
  size = "xl",
  bodyPadding = 2,
  zIndex = 1050,
  cancelDisabled = false,
  successDisabled = false,
  onSuccess = () => { },
  onCancel = () => { },
  onDismiss = () => { },
}) => {
  return (
    exists && (
      <div
        className={show ? 'modal fade in': 'modal fade'}
        tabIndex="-1"
        aria-hidden={show}
        style={{ display: show ? "block" : "none", zIndex, overflow: 'auto' }}
      >
        <div className={`modal-dialog modal-${size}`}>
          <div className="modal-content">
            {showHeader && (
              <div className="modal-header">
                <h4 className="modal-title">{title}
                  {canDismiss && (
                    <Button
                      type="button"
                      className="close"
                      aria-label="Close"
                      onClick={onDismiss}
                      icon="fa-times"
                    />
                  )}
                </h4>
              </div>
            )}
            <div className={`modal-body p-${bodyPadding}`}>
              {children}
            </div>
            {(cancelText || acceptText) && (
              <div className="modal-footer">
                {cancelText && (
                  <button
                    disabled={cancelDisabled}
                    className="btn rounded-0  btn-default"
                    onClick={onCancel}
                  >
                    {cancelText}
                  </button>
                )}
                {acceptText && (
                  <button
                    disabled={successDisabled}
                    className="btn rounded-0  btn-primary"
                    onClick={onSuccess}
                  >
                    {acceptText}
                  </button>
                )}
              </div>
            )}
          </div>
        </div>
      </div>
    )
  );
};

export default Dialog;
