import { createSlice } from "@reduxjs/toolkit";

export const { reducer: UserReducer, actions: UserActions } = createSlice(
  {
    name: "userSlice",
    initialState: {
      listData: {
        results: [],
        currentPage: 1,
        totalResults: 0,
        totalPages: 1,
      },
    },
    reducers: {
      setUsers: (state, { payload }) => {
        state.listData = { ...payload }
      },
    },
  }
);
