import { Box, Col, Content, Row } from "adminlte-2-react";
import DataGridComponent from "src/components/DataGridComponent";
import { useMemo } from "react";
import UserService from "src/services/UserService";
import { USER_ROLES, USER_STATUSES } from "src/constants/global";

const AdminListView = ({
  title = 'Super Users',
  description = 'here you can manage super users',
  ...rest
}) => {

  const fields = useMemo(() => {
    const _fields = [
      {
        id: "username",
        name: "username",
        label: "Username *",
        placeholder: "Enter Username",
        type: "text",
        cols: 4,
      },
      {
        id: "password",
        name: "password",
        label: "Password *",
        type: "password",
        cols: 4,
      },
      {
        id: "role",
        name: "role",
        label: "Role *",
        type: "select",
        cols: 4,
        options: [USER_ROLES.ADMIN, USER_ROLES.SUPERADMIN].map(role => {
          return {
            key: role,
            value: role
          }
        })
      },
      {
        id: "firstName",
        name: "firstName",
        label: "First Name *",
        type: "text",
        cols: 4,
      },
      {
        id: "middleName",
        name: "middleName",
        label: "Middle Name",
        type: "text",
        cols: 4,
      },
      {
        id: "lastName",
        name: "lastName",
        label: "Last Name",
        type: "text",
        cols: 4,
      },
    ]
    return _fields
  }, [])
  return (
    <Content title={title} subTitle={description} browserTitle={"Manage CTMS Users"}>
      <Box type="primary" title="Displaying CTMS users" icon="fa-users">
        <Row>
          <Col md={12}>
            <DataGridComponent
              modalSize="lg"
              actionColumnWidth={200}
              {...rest}
              canDelete={false}
              rowActionButtons={[
                {
                  text: 'Block',
                  icon: 'fa-times',
                  type: 'danger',
                  canShow: (row) => {
                    return row.status === USER_STATUSES.ACTIVE
                  },
                  onClick: async (id) => {
                    await UserService.blockUser(id)
                  }
                },
                {
                  text: 'Unblock',
                  icon: 'fa-check',
                  type: 'success',
                  canShow: (row) => {
                    return row.status === USER_STATUSES.DISABLED
                  },
                  onClick: async (id) => {
                    await UserService.unblockUser(id)
                  }
                }
              ]}
              entitySingle="Super User"
              entityPlural="Super Users"
              resource={`/api/v1/admin/superusers`}
              formOptions={{
                fields
              }}
              columns={[
                {
                  dataField: 'id',
                  text: 'User ID',
                  hidden: true,
                }, {
                  dataField: 'username',
                  text: 'Username',
                  sort: true,
                },
                {
                  dataField: 'role',
                  text: 'Role',
                  sort: true,
                },
                {
                  dataField: 'status',
                  text: 'Status',
                  sort: true,
                },
                {
                  dataField: 'firstName',
                  text: 'Name',
                  sort: true,
                  formatter: (c, row) => {
                    return [row.firstName, row.middleName, row.lastName].filter(x => !!x).join(" ")
                  }

                },
              ]}
            />
          </Col>
        </Row>
      </Box>
    </Content>
  );
};

export default AdminListView;
