
import { Box, Col, Content, Row } from "adminlte-2-react";
import DataGridComponent from "src/components/DataGridComponent";
import CommonService from "src/services/CommonService";
import { formatBytesToString, humanFriendlyDateFormat } from "src/utils/commonHelper";

const BackupsListView = ({
    title = 'Database backups',
    description = 'here you can see currently active users',
    ...rest
}) => {

    return (
        <Content title={title} subTitle={description} browserTitle={"Database backups"}>
            <Box type="success" title="Displaying latest 100 backup files" icon="fa-download">
                <Row>
                    <Col md={12}>
                        <DataGridComponent
                            modalSize="lg"
                            canAdd={false}
                            canEdit={false}
                            canDelete={false}
                            {...rest}
                            entitySingle="Backup"
                            entityPlural="Backups"
                            resource={`/api/v1/admin/db-backups`}
                            globalActionButtons={[
                                {
                                    text: 'Create Backup',
                                    icon: 'fa-plus',
                                    type: 'success',
                                    onClick: async () => {
                                        await CommonService.createDatabaseBackup()
                                    }
                                },]
                            }
                            columns={[
                                {
                                    dataField: 'filename',
                                    text: 'Backup file name',
                                },
                                {
                                    dataField: 'size',
                                    text: 'Size',
                                    formatter: (c, row) => {
                                        return formatBytesToString(row['size'])
                                    }
                                }, {
                                    dataField: 'createdOn',
                                    text: 'Created',
                                    formatter: (c, row) => {
                                        return humanFriendlyDateFormat(row['createdOn'])
                                    }
                                },
                            ]}
                        />
                    </Col>
                </Row>
            </Box>
        </Content>
    );
};

export default BackupsListView;
