import { GlobalActions } from "src/slices/globalSlice"
import store from "src/store/store"


export const showGlobalModalNotification = ({
    success = true,
    title = 'Please note',
    message,
    hideTs = 3000
}) => {
    window.scrollTo(0, 0)
    store.dispatch(GlobalActions.setGlobalMessage({
        type: success ? 'success' : 'danger',
        title,
        message
    }))
    // setTimeout(function () {
    //    
    // }, hideTs)
}

export const dismissGlobalError = () => {
    store.dispatch(GlobalActions.setGlobalMessage({
        type: '',
        title: '',
        message: ''
    }))
}