import * as yup from "yup";
import { BranchActions } from "src/slices/BranchSlice";
import { Box, Col, Content, Row } from "adminlte-2-react";
import DataGridComponent from "src/components/DataGridComponent";

const BranchListView = ({
  title = 'Branches',
  description = 'here you can manage branches',
  ...rest
}) => {
  return (
    <Content title={title} subTitle={description} browserTitle={"Manage Branch"}>
      <Box type="primary" title="Displaying branches" icon="fa-building">
        <Row>
          <Col md={12}>
            <DataGridComponent
              {...rest}
              entityPlural="Branches"
              entitySingle="Branch"
              resource={`/api/v1/admin/branches`}
              onDataUpdated={async (tableData) => {
                BranchActions.setBranches(tableData)
              }}
              defaultSorted={
                [{ dataField: 'id', order: 'asc' }]
              }
              columns={[
                {
                  dataField: 'id',
                  text: 'Branch ID',
                  hidden: true,
                }, {
                  dataField: 'title',
                  text: 'Title',
                  sort: true,
                },
                {
                  dataField: 'description',
                  text: 'Description',
                  sort: true,
                }
              ]}
              formOptions={
                {
                  fields: [
                    {
                      id: "title",
                      name: "title",
                      label: "Title",
                      placeholder: "Enter Title",
                      type: "text",
                      cols: 12,
                    },
                    {
                      id: "description",
                      name: "description",
                      label: "Branch Description",
                      type: "text",
                      cols: 12,
                    },
                  ],
                  validationSchema: yup.object().shape({
                    title: yup.string().required(),
                    description: yup.string(),
                  }),
                }
              }
            />
          </Col>
        </Row>
      </Box>
    </Content>
  );
};

export default BranchListView;
