import { createSlice } from "@reduxjs/toolkit";

export const { reducer: RankReducer, actions: RankActions } = createSlice(
  {
    name: "pageSlice",
    initialState: {
      listData: {
        results: [],
        currentPage: 1,
        totalResults: 0,
        totalPages: 1,
      },
      rank: null,
    },
    reducers: {
      setRanks: (state, { payload }) => {
        state.listData = { ...payload }
      },
    },
  }
);
