import httpClient from "src/utils/httpClient";

const rejectRegistration = async (id) => await httpClient.delete(`/api/v1/admin/registrations/${id}`);
const approveRegistration = async (id) => await httpClient.post(`/api/v1/admin/registrations/${id}`);

const RegistrationService = {
  rejectRegistration,
  approveRegistration,
};

export default RegistrationService;
