import * as yup from "yup";
import { Box, Col, Content, Row } from "adminlte-2-react";
import DataGridComponent from "src/components/DataGridComponent";
import { LocationActions } from "src/slices/LocationSlice";
import { useEffect, useMemo, useState } from "react";
import { parseQueryParams, pushQueryParams } from "src/utils/routerHelper";

const LocationListView = ({
  ...rest
}) => {

  const joinChar = '  ';

  const [level, setLevel] = useState(0)
  const [segments, setSegments] = useState([])

  const rowActionButtons = useMemo(() => {
    if (level > 1) {
      return []
    }
    return [{
      icon: 'fa-link',
      type: 'primary',
      text: 'Sublocations',
      onClick: (id, row) => {
        const _level = level + 1
        const _segments = [...segments, row.title]
        setSegments(_segments)
        setLevel(_level)
        pushQueryParams(rest.history, { level: _level, locationId: id, segments: _segments.join(',') })
      }
    }]
  }, [level, segments, rest.history])

  useEffect(() => {
    const _handler = () => {
      const params = parseQueryParams(rest.history)
      const segments = (params['segments'] || '').split(',').filter(segment => !!segment)
      setLevel(parseInt(params['level'] || 0))
      setSegments(segments)
    }
    _handler();
    const unlisten = rest.history.listen(_handler)
    return unlisten
  }, [rest.history])

  return (
    <Content title={['Manage Locations'].concat(segments).join(joinChar)} subTitle={['here you can manage locations'].concat(segments).join(joinChar)} browserTitle={segments.join(joinChar)}>
      <Box type="primary" title={['Displaying locations'].concat(segments).join(joinChar)} icon="fa-globe">
        <Row>
          <Col md={12}>
            <DataGridComponent
              {...rest}
              actionColumnWidth={320}
              entityPlural={['Locations'].concat(segments).join(joinChar)}
              entitySingle={['Location'].concat(segments).join(joinChar)}
              resource={`/api/v1/admin/locations`}
              onDataUpdated={async (tableData) => LocationActions.setLocations(tableData)}
              columns={[
                {
                  dataField: 'id',
                  text: 'Location ID',
                  hidden: true,
                }, {
                  dataField: 'title',
                  text: 'Title',
                  sort: true,
                },
                {
                  dataField: 'email',
                  text: 'Email',
                  sort: true,
                }
              ]}
              rowActionButtons={rowActionButtons}
              beforeSave={(values) => {
                const params = parseQueryParams(rest.history)
                values['locationId'] = params['locationId'] || null
                return values
              }}
              formOptions={
                {
                  fields: [
                    {
                      id: "title",
                      name: "title",
                      label: "Title",
                      placeholder: "Enter Title",
                      type: "text",
                      cols: 12,
                    },
                    
                    {
                      id: "email",
                      name: "email",
                      label: "Location Email",
                      type: "text",
                      cols: 12,
                    },
                  ],
                  validationSchema: yup.object().shape({
                    title: yup.string().required(),
                    email: yup.string().email(),
                    description: yup.string(),
                  })
                }
              }
            />
          </Col>
        </Row>
      </Box>
    </Content>
  );
};

export default LocationListView;
