import * as yup from "yup";

import { Box, Col, Content, Row } from "adminlte-2-react";
import DataGridComponent from "src/components/DataGridComponent";
import { useEffect, useMemo, useState } from "react";
import CommonService from "src/services/CommonService";
import UserService from "src/services/UserService";
import { USER_STATUSES } from "src/constants/global";

const UserListView = ({
  title = 'Users',
  description = 'here you can manage users',
  ...rest
}) => {

  const [locations, setLocations] = useState([])
  const [branches, setBranches] = useState([])
  const [ranks, setRanks] = useState([])
  const [record, setRecord] = useState(null)
  const [mode, setMode] = useState(null)
  console.log(mode);

  useEffect(() => {
    (async () => {
      setLocations(await CommonService.getLocationList())
      setBranches(await CommonService.getBranchesList())
      setRanks(await CommonService.getRanksList())
    })();
  }, [])

  const fields = useMemo(() => {
    const _fields = [
      {
        id: "username",
        name: "username",
        label: "Username *",
        placeholder: "Enter Username",
        type: "text",
        cols: 6,
      },
      {
        id: "password",
        name: "password",
        label: "Password *",
        type: "password",
        cols: 6,
      },
      {
        id: "firstName",
        name: "firstName",
        label: "First Name *",
        type: "text",
        cols: 4,
      },
      {
        id: "middleName",
        name: "middleName",
        label: "Middle Name",
        type: "text",
        cols: 4,
      },
      {
        id: "lastName",
        name: "lastName",
        label: "Last Name",
        type: "text",
        cols: 4,
      },
      {
        id: "locationId",
        name: "locationId",
        label: "Location *",
        type: "select",
        options: locations.map(l => {
          return {
            key: l.id,
            value: l.title
          }
        }),
        cols: 4,
      },
      {
        id: "branchId",
        name: "branchId",
        label: "Branch *",
        type: "select",
        options: branches.map(l => {
          return {
            key: l.id,
            value: l.title
          }
        }),
        cols: 4,
      },
      {
        id: "rankId",
        name: "rankId",
        label: "Rank *",
        type: "select",
        options: ranks.map(l => {
          return {
            key: l.id,
            value: l.title
          }
        }),
        cols: 4,
      },
      {
        id: "ip",
        name: "ip",
        label: "IP",
        type: "text",
        cols: 10,
      },
      {
        id: "port",
        name: "port",
        label: "Port",
        type: "text",
        cols: 2,
      },
      {
        id: "addressLine1",
        name: "addressLine1",
        label: "Address Line 1",
        type: "textarea",
        cols: 6,
      },
      {
        id: "addressLine2",
        name: "addressLine2",
        label: "Address Line 2",
        type: "textarea",
        cols: 6,
      },
    ]
    if (record && record?.id) {
     // return _fields.filter(field => !['password', 'username'].includes(field.name))
    }
    return _fields
  }, [locations, ranks, branches, record])
  return (
    <Content title={title} subTitle={description} browserTitle={"Manage Users"}>
      <Box type="primary" title="Displaying users" icon="fa-user">
        <Row>
          <Col md={12}>
            <DataGridComponent
              modalSize="lg"
              actionColumnWidth={300}
              {...rest}
              rowActionButtons={[
                {
                  text: 'Block',
                  icon: 'fa-times',
                  type: 'danger',
                  canShow: (row) => {
                    return row.status === USER_STATUSES.ACTIVE
                  },
                  onClick: async (id) => {
                    await UserService.blockUser(id)
                  }
                },
                {
                  text: 'Unblock',
                  icon: 'fa-check',
                  type: 'success',
                  canShow: (row) => {
                    return row.status === USER_STATUSES.DISABLED
                  },
                  onClick: async (id) => {
                    await UserService.unblockUser(id)
                  }
                }
              ]}
              entitySingle="User"
              entityPlural="Users"
              resource={`/api/v1/admin/users`}
              onFormShow={(row, mode) => {
                setRecord(row)
                setMode(mode)
              }}
              formOptions={
                {
                  fields,
                  validationSchema: yup.object().shape({
                    username: yup.string().required(),
                    firstName: yup.string().required(),
                    password: yup.string().required(),
                    locationId: yup.string().required(),
                    branchId: yup.string().required(),
                    rankId: yup.string().required(),
                  }),
                }
              }
              columns={[
                {
                  dataField: 'id',
                  text: 'User ID',
                  hidden: true,
                }, {
                  dataField: 'username',
                  text: 'Username',
                  sort: true,
                },
                {
                  dataField: 'branchId',
                  text: 'Branch',
                  sort: true,
                  formatter: (c, row) => {
                    return branches.find(b => `${b.id}` === `${row.branchId}`)?.title || '-'
                  }
                },
                {
                  dataField: 'rankId',
                  text: 'Rank',
                  sort: true,
                  formatter: (c, row) => {
                    return ranks.find(r => `${r.id}` === `${row.rankId}`)?.title || '-'
                  }
                },
                {
                  dataField: 'locationId',
                  text: 'Location',
                  sort: true,
                  formatter: (c, row) => {
                    return locations.find(l => `${l.id}` === `${row.locationId}`)?.title || '-'
                  }
                },
                {
                  dataField: 'status',
                  text: 'Status',
                  sort: true,
                },
                {
                  dataField: 'firstName',
                  text: 'Name',
                  sort: true,
                  formatter: (c, row) => {
                    return [row.firstName, row.middleName, row.lastName].filter(x => !!x).join(" ")
                  }

                },
                {
                  dataField: 'ip',
                  text: 'IP:Port',
                  formatter: (c, row) => {
                    return [row.ip, row.port].filter(x => !!x).join(":")
                  }

                },
              ]}
            />
          </Col>
        </Row>
      </Box>
    </Content>
  );
};

export default UserListView;
