
import { Box, Col, Content, Row } from "adminlte-2-react";
import { formatRelative, parseISO } from "date-fns";
import DataGridComponent from "src/components/DataGridComponent";
import { humanFriendlyDateFormat } from "src/utils/commonHelper";

const OnLineUsersView = ({
  title = 'Current Users',
  description = 'here you can see currently active users',
  ...rest
}) => {

  return (
    <Content title={title} subTitle={description} browserTitle={"Current Users"}>
      <Box type="success" title="Displaying current users" icon="fa-users">
        <Row>
          <Col md={12}>
            <DataGridComponent
              modalSize="lg"
              canAdd={false}
              canEdit={false}
              canDelete={false}
              {...rest}
              entitySingle="User"
              entityPlural="Users"
              resource={`/api/v1/admin/users/online`}
              columns={[
                {
                  dataField: 'username',
                  text: 'Username',
                  sort: true,
                },
                {
                  dataField: 'status',
                  text: 'Status',
                  sort: true,
                },
                {
                  dataField: 'firstName',
                  text: 'Name',
                  sort: true,
                  formatter: (c, row) => {
                    return [row.firstName, row.middleName, row.lastName].filter(x => !!x).join(" ")
                  }

                },
                {
                  dataField: 'ip',
                  text: 'IP:Port',
                  formatter: (c, row) => {
                    return [row.ip, row.port].filter(x => !!x).join(":")
                  }

                },
                {
                  dataField: 'lastVisited',
                  text: 'Last Visited',
                  formatter: (c, row) => {
                    return humanFriendlyDateFormat(row['lastVisited'])
                  }
                },
              ]}
            />
          </Col>
        </Row>
      </Box>
    </Content>
  );
};

export default OnLineUsersView;
