import * as yup from "yup";
import { NmsDeviceActions } from "src/slices/NmsDeviceSlice";
import { Box, Col, Content, Row } from "adminlte-2-react";
import DataGridComponent from "src/components/DataGridComponent";

const NmsDeviceListView = ({
  title = 'Manage NMS Devices',
  description = 'here you can manage devices',
  ...rest
}) => {
  return (
    <Content title={title} subTitle={description} browserTitle={title}>
      <Box type="primary" title="Displaying devices" icon="fa-laptop">
        <Row>
          <Col md={12}>
            <DataGridComponent
              {...rest}
              entityPlural="Devices"
              entitySingle="Device"
              resource={`/api/v1/admin/nms-devices`}
              onDataUpdated={async (tableData) => {
                NmsDeviceActions.setNmsDevices(tableData)
              }}
              columns={[
                {
                  dataField: 'id',
                  text: 'Device ID',
                  hidden: true,
                },{
                  dataField: 'name',
                  text: 'Name',
                  sort: true,
                }, {
                  dataField: 'ip',
                  text: 'IP',
                  sort: true,
                },
                {
                  dataField: 'port',
                  text: 'Port',
                  sort: true,
                }
              ]}
              formOptions={
                {
                  fields: [
                    {
                      id: "ip",
                      name: "ip",
                      label: "IP",
                      type: "text",
                      cols: 10,
                    },
                    {
                      id: "port",
                      name: "port",
                      label: "Port",
                      type: "text",
                      cols: 2,
                    },
                    {
                      id: "name",
                      name: "name",
                      label: "Device Name",
                      type: "text",
                      cols: 12,
                    },
                  ],
                  validationSchema: yup.object().shape({
                    ip: yup.string().required(),
                    name: yup.string().required(),
                  }),
                }
              }
            />
          </Col>
        </Row>
      </Box>
    </Content>
  );
};

export default NmsDeviceListView;
