import * as yup from "yup";

import { Box, Col, Content, Row } from "adminlte-2-react";
import DataGridComponent from "src/components/DataGridComponent";
import { RankActions } from "src/slices/RankSlice";

const RankListView = ({
  title = 'Ranks',
  description = 'here you can manage ranks',
  ...rest
}) => {
  return (
    <Content title={title} subTitle={description} browserTitle={"Manage Rank"}>
      <Box type="primary" title="Displaying ranks" icon="fa-sort-numeric-up">
        <Row>
          <Col md={12}>
            <DataGridComponent
              {...rest}
              entityPlural="Ranks"
              entitySingle="Rank"
              defaultSorted={
                [{ dataField: 'id', order: 'asc' }]
              }
              resource={`/api/v1/admin/ranks`}
              onDataUpdated={async(tableData) => {
                RankActions.setRanks(tableData)
              }}
              columns={[
                {
                  dataField: 'id',
                  text: 'Rank ID',
                  hidden: true,
                }, {
                  dataField: 'title',
                  text: 'Title',
                  sort: true,
                },
                {
                  dataField: 'description',
                  text: 'Description',
                  sort: true,
                }
              ]}
              formOptions={
                {
                  fields: [
                    {
                      id: "title",
                      name: "title",
                      label: "Title",
                      placeholder: "Enter Title",
                      type: "text",
                      cols: 12,
                    },
                    {
                      id: "description",
                      name: "description",
                      label: "Rank Description",
                      type: "text",
                      cols: 12,
                    },
                  ],
                  validationSchema: yup.object().shape({
                    title: yup.string().required(),
                    description: yup.string(),
                  }),
                }
              }
            />
          </Col>
        </Row>
      </Box>
    </Content>
  );
};
export default RankListView;
