import { connect } from "react-redux";
import UserService from "src/services/UserService";
import { CurrentUserActions } from "src/slices/currentUserSlice";
import AdminLTE, { Navbar, Sidebar, } from "adminlte-2-react";
import LoginForm from "src/components/LoginForm";
import { BrowserRouter } from "react-router-dom";
import { navigateToLogin } from "src/utils/routerHelper";
import {
  BRANCH_LIST_PAGE,
  LOCATION_LIST_PAGE,
  NAV_ITEMS,
  RANK_LIST_PAGE,
  REGISTRATION_LIST_PAGE,
  USER_LIST_PAGE,
  DASHBOARD_ROUTE,
  LOGOUT_ROUTE,
  NMS_LIST_PAGE,
  DEVICE_MAMAGEMENT_ROUTE,
  SUPER_USER_LIST_PAGE,
  ONLINE_USERS_ROUTE,
  BACKUPS_LIST,
  REPORTS_LIST_PAGE,
  SETTINGS_PAGE
} from "src/constants/global";

import React from "react";
import BranchListView from "src/modules/branches/views/BranchListView";
import LocationListView from "src/modules/locations/views/LocationListView";
import RankListView from "src/modules/ranks/views/RankListView";
import RegistrationListView from "src/modules/registrations/views/RegistrationListView";
import UserListView from "src/modules/users/UserListView";
import DashboardView from "src/modules/dashboard/views/DashboardView";
import GlobalToast from "src/components/GlobalToast";
import NmsDeviceListView from "src/modules/nms-devices/views/NmsDevicesListView";
import DeviceListView from "src/modules/device-directory/views/DevicesListView";
import AdminListView from "src/modules/users/AdminListView";
import OnLineUsersView from "src/modules/users/OnlineUsersView";
import BackupsListView from "src/modules/backups/BackupsListView";
import ReportListView from "src/modules/reports/ReportListView";
import SettingsListView from "src/modules/settings/SettingsListView";

const { Entry } = Navbar

const Layout = ({ triggerLogin, currentUser }) => {
  const isLoggedIn = !!currentUser
  if (!isLoggedIn) {
    return (
      <BrowserRouter>
        <GlobalToast />
        <LoginForm triggerLogin={triggerLogin} />
      </BrowserRouter>
    )
  }
  return (
    <React.Fragment>
      <GlobalToast />
      <AdminLTE theme="black" title={["TMCS", " Dashboard"]}>
        <Navbar.Core>
          {/* <Entry icon="fas-bell" labelType="danger">
            <Navbar.MessageItem text="Device 1 is offline" when={moment().subtract(2, 'hour')}></Navbar.MessageItem>
          </Entry> */}
          <Entry to={LOGOUT_ROUTE} icon="fa-power-off" labelType="danger" onClick={() => navigateToLogin()} />
        </Navbar.Core>
        <Sidebar.Core>
          {
            NAV_ITEMS.map((item, index) => {
              if (item.items && item.items.length > 0) {
                return (<Sidebar.Item onClick={item.onClick} key={index} icon={item.icon} text={item.title} to={item.path} color={item.color ?? 'gray'}>
                  {(item.items || []).map((childItem, childIndex) => {
                    return <Sidebar.Item onClick={childItem.onClick} key={`${index}-child-${childIndex}`} icon={childItem.icon} text={childItem.title} to={childItem.path} color={childItem.color ?? 'gray'} />
                  })}
                </Sidebar.Item>)
              }
              return <Sidebar.Item onClick={item.onClick} key={`${index}`} icon={item.icon} text={item.title} to={item.path} color={item.color ?? 'gray'} />
            })
          }
        </Sidebar.Core>

        <BranchListView path={BRANCH_LIST_PAGE} exact={true} />
        <LocationListView path={LOCATION_LIST_PAGE} exact={true} />
        <RankListView path={RANK_LIST_PAGE} exact={true} />
        <RegistrationListView path={REGISTRATION_LIST_PAGE} exact={true} />
        <UserListView path={USER_LIST_PAGE} exact={true} />
        <DashboardView path={DASHBOARD_ROUTE} exact={true} />
        <NmsDeviceListView path={NMS_LIST_PAGE} exact={true} />
        <DeviceListView path={DEVICE_MAMAGEMENT_ROUTE} exact={true} />
        <AdminListView path={SUPER_USER_LIST_PAGE} exact={true} />
        <OnLineUsersView path={ONLINE_USERS_ROUTE} exact={true} />
        <BackupsListView path={BACKUPS_LIST} exact={true} />
        <ReportListView path={REPORTS_LIST_PAGE} exact={true} />
        <SettingsListView path={SETTINGS_PAGE} exact={true} />
      </AdminLTE>
    </React.Fragment>

  );
};

const mapStateToProps = (state) => {
  return {
    currentUser: state.currentUserState?.user || null,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    triggerLogin: async ({ username, password }, history) => {
      const data = await UserService.loginUser(username, password);
      data?.success && dispatch(CurrentUserActions.setUserState(data.data));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Layout);
