import { Box, Col, Content, Row } from "adminlte-2-react";
import DataGridComponent from "src/components/DataGridComponent";
import RegistrationService from "src/services/RegistrationService";

const STATUSES = {
  APPROVED: 'APPROVED',
  REJECTED: 'REJECTED',
  PENDING: 'PENDING'
}

const RegistrationListView = ({
  title = 'Registrations',
  description = 'here you can manage registrations',
  ...rest
}) => {

  return (
    <Content title={title} subTitle={description} browserTitle={"Manage Registration"}>
      <Box type="primary" title="Displaying registrations" icon="fa-user">
        <Row>
          <Col md={12}>
            <DataGridComponent
              {...rest}
              actionColumnWidth={300}
              entitySingle="Registration"
              entityPlural="Registrations"
              resource={`/api/v1/admin/registrations`}
              rowActionButtons={[
                {
                  text: 'Approve',
                  icon: 'fa-check',
                  type: 'success',
                  canShow: (row) => {
                    return row.status === STATUSES.PENDING
                  },
                  onClick: async (id) => {
                    await RegistrationService.approveRegistration(id)
                  }
                },
                {
                  text: 'Reject',
                  icon: 'fa-times',
                  type: 'danger',
                  canShow: (row) => {
                    return row.status === STATUSES.PENDING
                  },
                  onClick: async (id) => {
                    await RegistrationService.rejectRegistration(id)
                  }
                }
              ]}
              canAdd={false}
              canEdit={false}
              canDelete={false}
              columns={[
                {
                  dataField: 'id',
                  text: 'Registration ID',
                  hidden: true,
                }, {
                  dataField: 'userName',
                  text: 'Username',
                  sort: true,
                },
                {
                  dataField: 'status',
                  text: 'Status',
                  sort: true,
                },
                {
                  dataField: 'firstName',
                  text: 'First Name',
                  sort: true,

                },
                {
                  dataField: 'middlename',
                  text: 'Middle Name',
                  sort: true,
                },
                {
                  dataField: 'lastName',
                  text: 'Last Name',
                  sort: true,
                },

                {
                  dataField: 'ip',
                  text: 'IP',
                },
                {
                  dataField: 'port',
                  text: 'Port',
                },
              ]}
            />
          </Col>
        </Row>
      </Box>
    </Content>
  );
};


export default RegistrationListView;
