import { createSlice } from "@reduxjs/toolkit";

export const { reducer: LocationReducer, actions: LocationActions } = createSlice(
  {
    name: "locationSlice",
    initialState: {
      listData: {
        results: [],
        currentPage: 1,
        totalResults: 0,
        totalPages: 1,
      },
      _location: null,
    },
    reducers: {
      setLocations: (state, { payload }) => {
        state.listData = { ...payload }
      },
    },
  }
);
