import { configureStore } from "@reduxjs/toolkit";
import thunk from "redux-thunk";
import { RegistrationReducer } from "src/slices/RegistrationSlice";
import { RankReducer } from "src/slices/RankSlice";
import { BranchReducer } from "src/slices/BranchSlice";
import { LocationReducer } from "src/slices/LocationSlice";
import { GlobalReducer } from "src/slices/globalSlice";
import { CurrentUserReducer } from "src/slices/currentUserSlice";
import { NmsDeviceReducer } from "src/slices/NmsDeviceSlice";
import { DeviceReducer } from "src/slices/DeviceSlice";
import { UserReducer as UserManagementReducer } from "src/slices/UserSlice";

export default configureStore({
  reducer: {
    globalState: GlobalReducer,
    currentUserState: CurrentUserReducer,
    rankState: RankReducer,
    branchState: BranchReducer,
    locationState: LocationReducer,
    registrationState: RegistrationReducer,
    nmsState: NmsDeviceReducer,
    deviceState: DeviceReducer,
    userState: UserManagementReducer
  },
  middleware: [thunk],
});
