import { userKEY } from "src/slices/currentUserSlice"
import LocalStorageHelper from "./localStorageHelper"

export const pushQueryParams = (history, params = {}, append = true, replace = false) => {
    const queryParams = parseQueryParams(history)
    params = append ? { ...queryParams, ...params } : params
    const finalParams = {}
    Object.keys(params).forEach(p => {
        if (typeof params[p] !== 'undefined' && params[p] !== 'undefined' && params[p] !== 'null' && params[p] !== null) {
            finalParams[p] = params[p]
        }
    })
    replace ? history.replace({
        pathname: history.location.pathname,
        search: (new URLSearchParams(finalParams)).toString()
    }) : history.push({
        pathname: history.location.pathname,
        search: (new URLSearchParams(finalParams)).toString()
    })
}

export const parseQueryParams = (history, key = null, defaultVal = null) => {
    const params = {}
    const p = new URLSearchParams(history.location.search)
    p.forEach((v, k) => {
        params[k] = v
    })
    if (key != null) {
        return params[key] || defaultVal
    }
    return params
}

export const getCurrentStateParam = (history, param = 'id') => {
    const state = (history.location.state || {})
    if (state && state[param]) {
        return state[param]
    }
    return null
}

export const isCurrentRoute = (history, matchingRouteName) => {
    console.log(history, matchingRouteName)
    return false
}

export const navigateToPath = (history, path, state = {}, clearPrevious = false) => {

    let pathname = path

    Object.keys(state).forEach(key => {
        pathname = pathname.replace(`:${key}`, state[key])
    })

    if (clearPrevious) {
        history.replace({
            pathname,
            state
        })
    } else {
        history.push({
            pathname,
            state
        })
    }
}

export const navigateToHome = (history) => {
    const params = parseQueryParams(history)
    const path = (params['redirect'] || '/').replace(`${window.location.protocol}://${window.location.host}`, '')
    navigateToPath(history, path.substring())
}

export const navigateToSubPath = (history, subPath, clearPrevious = false) => {
    const pathname = history.location.pathname + subPath
    navigateToPath(history, pathname, clearPrevious)
}


export const navigateToLogin = (route = '/login') => {
    LocalStorageHelper.delete(userKEY)
    window.location = route
}
