
import { Box, Col, Content, Row } from "adminlte-2-react";
import { formatRelative, parseISO } from "date-fns";
import DataGridComponent from "src/components/DataGridComponent";
import { humanFriendlyDateFormat } from "src/utils/commonHelper";

const ReportListView = ({
    title = 'Reports',
    description = 'here you can see various activity by users',
    ...rest
}) => {

    return (
        <Content title={title} subTitle={description} browserTitle={"Reports"}>
            <Box type="success" title="Displaying activity reports" icon="fa-chart-line">
                <Row>
                    <Col md={12}>
                        <DataGridComponent
                            modalSize="lg"
                            canAdd={false}
                            canEdit={false}
                            canDelete={false}
                            {...rest}
                            entitySingle="User"
                            entityPlural="Users"
                            resource={`/api/v1/admin/reports`}
                            columns={[
                                {
                                    dataField: 'entity',
                                    text: 'Entity',
                                    sort: true,
                                    formatter: (c, row) => {
                                        return <strong>{`${row['entity']}`.toUpperCase()}</strong>
                                    },
                                },
                                {
                                    dataField: 'entityAction',
                                    text: 'Action',
                                    sort: true,
                                    formatter: (c, row) => {
                                        return row['entityAction'].split('_').join(" ")
                                    },
                                },
                                {
                                    dataField: 'modifiedById',
                                    text: 'Action Taken By',
                                    sort: true,
                                    formatter: (c, row) => {
                                        return row['modifiedById'] || '-'
                                    },
                                },
                                {
                                    dataField: 'additionalInfo',
                                    text: 'Comments',
                                    sort: true,
                                    formatter: (c, row) => {
                                        return `${row['additionalInfo']}`
                                    },
                                },
                                {
                                    dataField: 'createdAt',
                                    text: 'Action Performed on',
                                    sort: true,
                                    formatter: (c, row) => {
                                        return `${humanFriendlyDateFormat(row['createdAt'])}`
                                    },
                                }
                            ]}
                        />
                    </Col>
                </Row>
            </Box>
        </Content>
    );
};

export default ReportListView;
