import { createSlice } from "@reduxjs/toolkit";

export const { reducer: BranchReducer, actions: BranchActions } = createSlice(
  {
    name: "pageSlice",
    initialState: {
      listData: {
        results: [],
        currentPage: 1,
        totalResults: 0,
        totalPages: 1,
      },
      branch: null,
    },
    reducers: {
      setBranches: (state, { payload }) => {
        state.listData = { ...payload }
      },
    },
  }
);
