import * as yup from "yup";
import { DeviceActions } from "src/slices/DeviceSlice";
import { Box, Col, Content, Row } from "adminlte-2-react";
import DataGridComponent from "src/components/DataGridComponent";
import CommonService from "src/services/CommonService";
import { useEffect, useState } from "react";

const DeviceListView = ({
  title = 'Manage Devices',
  description = 'here you can manage devices directory',
  ...rest
}) => {

  const [locations, setLocations] = useState([])
  useEffect(() => {
    (async () => {
      setLocations(await CommonService.getLocationList())
    })();
  }, [])

  return (
    <Content title={title} subTitle={description} browserTitle={title}>
      <Box type="primary" title="Displaying devices" icon="fa-tools">
        <Row>
          <Col md={12}>
            <DataGridComponent
              {...rest}
              entityPlural="Devices"
              entitySingle="Device"
              resource={`/api/v1/admin/devices`}
              onDataUpdated={async (tableData) => {
                DeviceActions.setDevices(tableData)
              }}
              columns={[
                {
                  dataField: 'id',
                  text: 'Device ID',
                  hidden: true,
                },{
                  dataField: 'name',
                  text: 'Name',
                  sort: true,
                },
                {
                  dataField: 'locationId',
                  text: 'Location',
                  sort: true,
                  formatter: (c, row) => {
                    console.log(row)
                    return locations.find(l => `${l.id}` === `${row.locationId}`)?.title || '-'
                  }
                },
                 {
                  dataField: 'ip',
                  text: 'IP',
                  sort: true,
                },
                {
                  dataField: 'port',
                  text: 'Port',
                  sort: true,
                }
              ]}
              formOptions={
                {
                  fields: [
                    {
                      id: "ip",
                      name: "ip",
                      label: "IP",
                      type: "text",
                      cols: 10,
                    },
                    {
                      id: "port",
                      name: "port",
                      label: "Port",
                      type: "text",
                      cols: 2,
                    },
                    {
                      id: "name",
                      name: "name",
                      label: "Device Name",
                      type: "text",
                      cols: 12,
                    },
                    {
                      id: "locationId",
                      name: "locationId",
                      label: "Location *",
                      type: "select",
                      options: locations.map(l => {
                        return {
                          key: l.id,
                          value: l.title
                        }
                      }),
                      cols: 12,
                    },
                  ],
                  validationSchema: yup.object().shape({
                    ip: yup.string().required(),
                    name: yup.string().required(),
                  }),
                }
              }
            />
          </Col>
        </Row>
      </Box>
    </Content>
  );
};

export default DeviceListView;
