import { createSlice } from "@reduxjs/toolkit";

export const { actions: GlobalActions, reducer: GlobalReducer } = createSlice({
  name: "globalSlice",
  initialState: {
    errors: null,
    globalMessage: {
      title: '',
      type: '',
      message: ''
    },
    fileManagerContext: {
      context: null,
      show: false,
    },
  },
  reducers: {
    setGlobalMessage: (state, { payload }) => {
      const { type, title, message } = payload
      state.globalMessage = { ...state.globalError, type, title, message }
    },
    setFileManagerContext: (state, { payload }) => {
      state.fileManagerContext = {
        ...state.fileManagerContext,
        ...payload,
        show: payload?.context ? true : payload.show,
      };
    },
  },
});
